"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click=\"through(scope.row)\">通过</el-button>\n            <el-button type=\"danger \"\n                       size=\"small\"\n                       @click=\"reject(scope.row)\">驳回</el-button>\n          </template>\n        </el-table-column>\n      </el-table>\n\n      <el-dialog title=\"详情\"\n                 center\n                 :visible.sync=\"detailVisible\">\n        <table class=\"table\">\n          <tr>\n            <th>油品名称</th>\n            <th>总后台的油品名称</th>\n            <th>油品介绍</th>\n            <th>升数</th>\n            <th>结算价</th>\n            <th>型号</th>\n            <th>油品图片</th>\n          </tr>\n          <tr v-for='(item,index) in detailList'\n              :key=\"index\">\n            <td>\n              {{item.oil_name}}\n            </td>\n            <td>\n              {{item.little_name}}\n            </td>\n            <td>\n              <el-popover placement=\"top-start\"\n                          title=\"油品介绍\"\n                          width=\"200\"\n                          trigger=\"hover\"\n                          :content=\"item.about\">\n                <el-button slot=\"reference\"\n                           type=\"text\"\n                           class=\"ellipsis\"\n                           style=\"width:100px\">{{item.about}}</el-button>\n              </el-popover>\n            </td>\n            <td>\n              {{item.rise}}\n            </td>\n            <td>\n              {{item.price}}\n            </td>\n            <td>\n              {{item.standard}}\n            </td>\n            <td>\n              <img :src=\"item.cover\"\n                   ref=\"images\">\n            </td>\n          </tr>\n        </table>\n      </el-dialog>\n      <div class=\"page_center\"\n           v-show=\"page&&page>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      list: [],\n      seCurId: '',\n      token: window.sessionStorage.getItem('bbytoken'), //token令牌\n      page: 0,\n      pageCount: 0,\n      detailList: [],\n      detailVisible: false, //详情弹框状态\n      throughLoading: false, //通过加载Loading\n      rejectLoading: false, //驳回Loading\n    }\n  },\n\n  methods: {\n    paging (e) {  //获取供应商列表当前页\n      this.page = e\n      this.init()\n    },\n\n    async init () {\n      try {\n        const res = await this.$axios.post('admin/SmCash/smApplyCashList', { token: this.token, page: this.page })\n        this.list = res.data.data.list || []\n        this.pageCount = res.data.data.rows || 0\n\n\n      } catch (err) {\n        throw (err)\n      }\n    },\n    detail () {  //油品详情\n      this.$nextTick(() => {\n        const ViewerRef = this.$refs.images\n        Viewer(ViewerRef)\n      })\n    },\n    //通过审核\n    through (row) {\n      this.$confirm('此操作将通过审核, 是否继续?', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning'\n      }).then(async () => {\n        try {\n          this.throughLoading = true\n          const res = await this.$axios.post('admin/SmCash/cashHandle', { token: this.token, sm_id: row.sm_id, id: row.id, total_money: row.total_money })\n          this.throughLoading = false\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: 'success' })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n        } catch (err) {\n          throw (err)\n        }\n      }).catch(() => { });\n    },\n    //驳回\n    reject (row) {\n\n      this.$prompt('请输入驳回理由', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        inputPattern: /\\S/,\n        inputErrorMessage: '驳回理由不能为空'\n      }).then(async ({ value }) => {\n        try {\n          this.rejectLoading = true\n          const res = await this.$axios.post('admin/SmCash/cashReject', { token: this.token, sm_id: row.sm_id, reason: value, id: row.id, total_money: row.total_money })\n          this.rejectLoading = false\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: 'success' })\n            this.init()\n          } else {\n            this.$message.errof(res.data.msg)\n          }\n        } catch (err) {\n          throw (err)\n        }\n      }).catch(() => { });\n    },\n\n    // 权限列表,当前所在权限页  \n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var arr = res.data.data || [];\n          this.authList = arr;\n          console.log(this.authList)\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n  created () {\n    this.erAuth()\n  },\n  mounted () {\n    this.init() //初始化数据  \n  },\n}\n</script>\n<style scoped>\n.table img {\n  width: 50px;\n  height: 50px;\n  vertical-align: middle;\n  margin: 10px 0;\n}\n.ellipsis {\n  width: 80px !important;\n  white-space: nowrap !important;\n  text-overflow: ellipsis !important;\n  overflow: hidden !important;\n}\n</style>"],"sourceRoot":""}]);

